#onepage-content{position:relative;display:flex;flex-direction:column;justify-content:space-around;align-items:center;width:auto;height:auto;margin:1rem;overflow-y:scroll}@media(max-width: 575px){#onepage-content{margin-top:3rem}}@media(min-width: 576px){#onepage-content{margin-top:56px}}.glass-card{box-sizing:border-box;position:relative;background:rgba(255,255,255,.6);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:.75rem;border:1px solid rgba(255,255,255,.2);box-shadow:0 0 2px 1px rgba(0,0,0,.2);padding:2rem;flex-grow:1;width:100%;margin:1rem 0;display:flex}@media(max-width: 575px){.glass-card{flex-direction:column;justify-content:flex-start;align-items:center}}@media(min-width: 576px){.glass-card{flex-direction:row}}.glass-card-head{padding-bottom:1rem}.glass-card-head h2{font-family:"Permanent Marker",cursive;font-size:2rem;color:#203060;line-height:1;margin:0}.glass-card-content{font-family:"Montserrat",sans-serif;font-size:1rem;color:#203060;margin:0;padding:0;line-height:1.5}.glass-card-content p{padding:0;margin:0}@media(max-width: 575px){.glass-card-content{margin-top:1rem}}#to-top svg{transform:rotate(180deg)}@media(max-width: 575px){#to-top{display:flex;justify-content:center;align-items:center;text-align:center;padding:0 1rem;font-size:2rem;background:rgba(255,255,255,.4);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:.75rem .75rem 0 0;border:1px solid rgba(255,255,255,.2);position:fixed;bottom:0;left:50%;transform:translate(-50%, 0);color:#000}}@media(min-width: 576px){#to-top{display:none}}
#top-menu{position:fixed;top:0;width:100%;z-index:3}#header{width:100%;display:flex;justify-content:center}@media(max-width: 575px){#header{position:relative;top:-176px;flex-direction:column;align-items:center;transition:top .1s ease-out}}@media(min-width: 576px){#header{position:relative;top:0}}.show{top:0 !important}.links-list{display:inline-flex;list-style-type:none}@media(max-width: 575px){.links-list{background:#fff;flex-direction:column;align-items:center;width:auto;padding:.5rem .5rem;margin:0 auto}}@media(min-width: 576px){.links-list{margin-top:0;background:rgba(255,255,255,.4);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:0 0 .75rem .75rem;border:1px solid rgba(255,255,255,.2);flex-direction:row;justify-content:center;align-items:space-between}}.list-item{padding:.5rem;text-shadow:0 0 1px #000;line-height:1}.navbar-link{font-family:"Permanent Marker",cursive;color:#203060;font-size:1.5rem;line-height:1;text-decoration:none}.navbar-link:hover{color:#885090}@media(max-width: 575px){#dd-tag{width:auto;height:auto;margin-right:auto;margin-left:auto;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;text-align:center;padding:0 .5rem .5rem 1rem;font-size:2rem;background-color:#fff;border-radius:0 0 .75rem .75rem;border:1px solid rgba(255,255,255,.2);border-top:0}}@media(min-width: 576px){#dd-tag{display:none}}
